import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import moment from "moment"
import localization from 'moment/locale/ar-dz';


moment.updateLocale('ar-dz', localization);
ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
