import React from 'react';
import moment from 'moment'
import {faClock} from '@fortawesome/free-regular-svg-icons'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

function toSnippet(content, maxLength) {
    return content.length >= maxLength
        ? content.substring(0, maxLength - 3) + "..."
        : content;
}

function NewsCard(props) {

    const isOnlyText = !props.imgLink ;
    const articleDate = new Date(props.dateTime).toString();
    const readableDate = moment(new Date(articleDate)).format('LLLL');
    const relativeDate = moment(new Date(articleDate)).fromNow();
    return (
        <div
            onClick={() => window.open(props.link, '_blank')}
            className="clickable news-card-flex shadowed">
            <div className="news-card-column alternative">

                {getImage(props.imgLink)}
                {getTitle(props.title, props.link)}
                <div className="content-snippet">{toSnippet(props.description, (isOnlyText? 250 : 100))}</div>
                {getHeader(readableDate, relativeDate, props.sourceName)}
            </div>
            
            <div className="news-card-row main">
                <div className="news-content">
                    {getHeader(readableDate, relativeDate, props.sourceName)}
                    {getTitle(props.title, props.link)}
                    <div className="content-snippet">{toSnippet(props.description, (isOnlyText? 500 : 250))}</div>
                </div>
                {getImage(props.imgLink)}
            </div>
        </div>
    )
}

function getHeader(readableDate, relativeDate, sourceName) {

    return (
        <div title={readableDate} className="article-header">
            <span><FontAwesomeIcon icon={faClock}/> {relativeDate}</span>
            <span>-</span>
            <span className="source-header" href="#">@{sourceName}</span>
        </div>
    )
}

function getTitle(title, link) {
    return (
        <div className="article-title">
            <a href={link} target="_blank" rel="noopener noreferrer">{title}</a>
        </div>
    )
}

function getImage(imgLink) {
    if (imgLink) {
        return (
            <div className="news-img-container">
                <img className="news-img" src={imgLink} alt="placeHolder"></img>
            </div>
        )
    } else {
        return;
    }
}

export default React.memo(NewsCard)