import React from 'react';
import NewsCard from './NewsCard'
import './News.scss'

import {faChevronDown, faSyncAlt, faExclamation} from '@fortawesome/free-solid-svg-icons'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import { useQuery, gql } from '@apollo/client';

const GET_ARTICLES = gql`query Articles($cursor: String) {articles(first: 10, after: $cursor) {
    edges {
      node {
        id, pubDateTime, title, sourceName, 
        articleUrl,  articleSummary, imageUrl
      }
      cursor
    } 
    pageInfo {startCursor, endCursor, hasNextPage}
    totalCount
  }}`;

function toArticles(articles) {
    return articles?.edges?.map( x=> x.node) || [];
}
function hasNextPage(articles) {
    return articles?.pageInfo?.hasNextPage;
}

function News() {

    const { loading, error, data, fetchMore } = useQuery(GET_ARTICLES);
    
    function loadMore () {
        fetchMore({
            variables: {
              cursor: data?.articles?.pageInfo?.endCursor
            }
        });
    }
    
    return (
        
        <div className="news-flex">
            {toArticles(data?.articles).map((article) =>
                (
                    <NewsCard
                        key={article.id}
                        title={article.title}
                        description={article.articleSummary}
                        link={article.articleUrl}
                        sourceName={article.sourceName}
                        dateTime={article.pubDateTime}
                        imgLink={article.imageUrl}
                    />
                )
            )}

            <div className={"load-more clickable " + (error ? "red": "")} 
                onClick={() => hasNextPage(data?.articles) && !loading ? loadMore() : undefined}>
                {loading ? (<FontAwesomeIcon icon={faSyncAlt} spin/>)
                 : error ?  (<FontAwesomeIcon  icon={faExclamation}/>)
                 :(<FontAwesomeIcon icon={faChevronDown} />)
                }
            </div>
        </div>

    )
}

export default News

